import React from "react";
import PropTypes from "prop-types";

import st from "./style";

import IconBuy from '../../icons/IconBuy';

import ImageColorFilter from '../../cards/ImageColorFilter';

import IconPlaceHolderCover from '../../icons/IconPlaceHolderCover';

class ProductPriceCard extends React.Component {

  constructor(props) {

    super(props);
    this.state = {
      
    };
  }

  render() {
    const formatPrice = price => {
      if(!price)return "";
      return `R$${price.replace('.',',')}`;
    }

    return (
      <div style={{...st.container,...this.props.style}}>

        <div style={st.ctnTop}>
        {
          this.props.img?
          <div>
            <ImageColorFilter 
              src={this.props.img} 
              style={st.img} 
              fromColor='rgba(255, 255, 255, 0) 0%' 
              toColor='rgba(0, 0, 0, 0.5) 100%' 
            />
          </div>
          :
          <div style={st.ctnPlaceholder}>
            <IconPlaceHolderCover style={st.iconPlaceHolder}/>
          </div>
          
        }

          <div style={st.ctnContent}>
            <p style={st.pAuthor}>{this.props.author}</p>
            <p style={st.pTitle}>{this.props.productTitle}</p>
            <div style={st.iconBuyCtn}><IconBuy /></div>
          </div>

        </div>

        <div style={st.ctnBottom}>
          <div style={st.ctnBLeft}>
            <p style={st.pPrice}>{formatPrice(this.props.price)}</p>
          </div>
          <div style={st.ctnBRight}>
            <p style={st.pUnit}>{this.props.unit}</p>
            <p style={st.pCount}>{this.props.count}</p>
          </div>
          
        </div> 

      </div>
    );
  }
}

ProductPriceCard.propTypes = {
  productTitle: PropTypes.string,
  author: PropTypes.string,
  price: PropTypes.string,
  unit: PropTypes.string,
  count: PropTypes.string,
  img: PropTypes.string,
  style: PropTypes.object,
};
ProductPriceCard.defaultProps = {
  style: {},
};

export default ProductPriceCard;
